body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
  background-color: #FFFFFF;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
.list-style-none {
  list-style: none;
  padding: 0;
  margin: 0;
}
.page-body {
  width: 320px;
  margin: 0 auto;
  background-color: #F2F2F2;
}
@media (min-width: 768px) {
  .page-body {
    width: 768px;
    background-color: #F2F2F2;
  }
}
@media (min-width: 1200px) {
  .page-body {
    min-width: 1200px;
    padding: 0 40px 0 40px;
    background-color: #F2F2F2;
  }
}
.page-wrapper {
  background-color: #FFFFFF;
}
@media (min-width: 1200px) {
  .page-wrapper {
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}
.page-header {
  position: relative;
}
@media (max-width: 767px) {
  .page-header {
    padding-bottom: 56px;
  }
}
.page-header__logo {
  display: block;
  position: absolute;
  transform: translateX(107%);
  width: 102px;
  height: 84px;
}
@media (max-width: 767px) {
  .page-header__logo--menu-opened {
    transform: translate(107%, 264%);
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    transform: translateX(326%);
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 187px;
    height: 97px;
    transform: translateX(271%);
  }
}
.page-header__logo-image {
  width: 100%;
}
.main-nav__link {
  text-decoration: none;
  background-color: #81B3D2;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 40px;
  padding: 9px 0 5px 0;
}
@media (min-width: 768px), (min-width: 1200px) {
  .main-nav__link {
    background-color: #FFFFFF;
    color: #000000;
    font-size: 14px;
    padding: 8px 20px 8px 20px;
  }
  .main-nav__link:hover {
    color: #81B3D2;
  }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__link:focus {
    color: #766357;
  }
  .main-nav__link:focus:hover {
    color: #604E43;
  }
  .main-nav__link:focus:active {
    color: rgba(118, 99, 87, 0.3);
  }
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    flex-basis: 153px;
  }
  .main-nav__item:nth-child(2) {
    margin-right: auto;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    flex-basis: 239px;
  }
  .main-nav__item:nth-child(2) {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--opened .main-nav__item {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav__item a {
    color: #ffffff;
    background-color: #81B3D2;
    font-size: 18px;
  }
  .main-nav__item:not(:first-child) {
    border-top: 1px solid #9ac2db;
  }
  .main-nav__item:not(:last-child) {
    border-bottom: 1px solid #678fa8;
  }
  .main-nav__item:last-child a {
    box-shadow: inset 0 -11px 5px -7px rgba(0, 0, 0, 0.2);
  }
  .main-nav__item a:hover {
    background-color: #669EC0;
  }
  .main-nav__item a:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496BD;
  }
  .main-nav__item a:disabled {
    background-color: #E5E5E5;
  }
}
.main-nav__toggle {
  position: absolute;
  display: block;
  content: "";
  background-image: url("../../img/icon-menu.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  top: 8px;
  right: 11px;
  border: none;
  z-index: 1;
  background-color: #FFFFFF;
  cursor: pointer;
  padding: 0;
}
@media (max-width: 767px) {
  .main-nav__toggle--menu-opened {
    top: 231px;
    right: 11px;
  }
  .main-nav__toggle--icon-hidden {
    display: none;
  }
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__cross {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__cross--menu-opened {
    position: absolute;
    display: block;
    content: "";
    background-image: url("../../img/icon-cross.svg");
    background-position: center;
    background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    top: 8px;
    right: 11px;
    border: none;
    z-index: 1;
    background-color: #81B3D2;
    cursor: pointer;
    padding: 0;
  }
  .main-nav__cross--menu-opened:hover {
    opacity: 0.6;
  }
  .main-nav__cross--menu-opened:active {
    opacity: 0.3;
  }
  .main-nav__cross--icon-hidden {
    display: none;
  }
}
